.container
  {
    width: 100%;
     padding-left: 20px;
     padding-right: 20px;
     margin: 0 auto;
    }
    @media screen and (min-width: 320px) {
     .container {
       width: 320px;
     }
    }
    @media screen and (min-width: 321px) and (max-width: 479px) {
     .container {
       width: 100%;
     }
    }
    @media screen and (min-width: 480px) {
     .container {
       width: 480px;
     }
    }
    @media screen and (min-width: 768px) {
     .container {
       width: 768px;
       padding-left: 32px;
       padding-right: 32px;
     }
    }
    @media screen and (min-width: 1024px) {
      .container {
        width: 1024px;
        padding-left: 32px;
        padding-right: 32px;
        }
      }
    @media screen and (min-width: 1280px) {
     .container {
       width: 1280px;
       padding-left: 32px;
       padding-right: 32px;
     }
    }